

function ColorsPalette(props) {
  return(
    <div>
      <h1>5가지 컬러 추천</h1>
      <div>
        <button disabled={props.number === 1 ? true : false} onClick={()=>{props.setNumber(1)}}>1개</button>
        <button disabled={props.number === 3 ? true : false} onClick={()=>{props.setNumber(3)}}>3개</button>
        <button disabled={props.number === 5 ? true : false} onClick={()=>{props.setNumber(5)}}>5개</button>
        <button disabled={props.number === 7 ? true : false} onClick={()=>{props.setNumber(7)}}>7개</button>
      </div>
      <button onClick={props.axiosColors}>컬러 확인!</button>
    </div>
  )
}

export default ColorsPalette;